.album-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.album-item {
  margin: 20px;
  width: 16%;
  text-align: center;
}

.album-item:hover {
  background: -webkit-gradient(linear, left top, left bottom, from(#9a0a0a), to(#1a1a1a));
  background: -webkit-linear-gradient(#9a0a0a, #1a1a1a);
  background: -o-linear-gradient(#9a0a0a, #1a1a1a);
  background: linear-gradient(#9a0a0a, #1a1a1a);
  color: #fff;
  text-decoration: none;
}

.album-item__title {
  margin: 10px;
  text-align: center;
}

.album-item__avatar {
  width: 160px;
  border-radius: 50%;
}

.album-item__space {
  margin-bottom: 7px;
}



